@media (max-width: 768px) {
  .header-nav {
    flex-direction: column;
    align-items: flex-start;
    top: 1rem;
    left: 1rem;
    right: 1rem;
  }

  .header-nav ul {
    flex-direction: column;
    gap: 0.75rem;
    margin-top: 1rem;
    font-size: 0.9rem;
  }

  .logo {
    height: 24px;
  }
}

.header-nav {
  position: absolute;
  top: 2rem;
  right: 2rem;
  left: 2rem;
  z-index: 10;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo-container {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.logo {
  height: 28px;
  opacity: 0.8;
  transition: opacity 0.3s ease;
}

.logo:hover {
  opacity: 1;
  transform: scale(1.05);
}

.header-nav ul {
  display: flex;
  gap: 1.5rem;
  list-style: none;
  font-family: var(--font-body);
  font-size: 0.95rem;
  font-weight: 500;
  color: var(--color-navy);
}

.header-nav li {
  cursor: pointer;
  transition: color 0.3s ease;
}

.header-nav li:hover {
  color: var(--color-accent);
}
